"use client";

import { useState, useRef, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";

interface Message {
  id: string;
  senderId: string;
  senderName: string;
  senderAvatar: string;
  content: string;
  timestamp: Date;
  type: 'text' | 'image' | 'product';
  isRead: boolean;
  attachments?: {
    type: 'image' | 'product';
    url: string;
    name?: string;
  }[];
  replyTo?: string;
}

interface Conversation {
  id: string;
  participant: {
    id: string;
    name: string;
    avatar: string;
    isOnline: boolean;
    lastSeen?: Date;
  };
  lastMessage: Message;
  unreadCount: number;
  isHost: boolean;
}

const EnhancedChatInterface = () => {
  const [selectedConversation, setSelectedConversation] = useState<string | null>(null);
  const [newMessage, setNewMessage] = useState("");
  const [replyingTo, setReplyingTo] = useState<Message | null>(null);
  const [showImageUpload, setShowImageUpload] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const [conversations] = useState<Conversation[]>([
    {
      id: '1',
      participant: {
        id: 'host1',
        name: 'Jessica Martinez',
        avatar: '/api/placeholder/40/40',
        isOnline: true
      },
      lastMessage: {
        id: 'msg1',
        senderId: 'host1',
        senderName: 'Jessica Martinez',
        senderAvatar: '/api/placeholder/40/40',
        content: 'The Fenty foundation is perfect for your skin tone! When would you like to try it?',
        timestamp: new Date(Date.now() - 300000), // 5 minutes ago
        type: 'text',
        isRead: false
      },
      unreadCount: 2,
      isHost: true
    },
    {
      id: '2',
      participant: {
        id: 'user1',
        name: 'Sarah Johnson',
        avatar: '/api/placeholder/40/40',
        isOnline: false,
        lastSeen: new Date(Date.now() - 3600000) // 1 hour ago
      },
      lastMessage: {
        id: 'msg2',
        senderId: 'user1',
        senderName: 'Sarah Johnson',
        senderAvatar: '/api/placeholder/40/40',
        content: 'Thank you so much! The lipstick was amazing ðŸ’„',
        timestamp: new Date(Date.now() - 7200000), // 2 hours ago
        type: 'text',
        isRead: true
      },
      unreadCount: 0,
      isHost: false
    }
  ]);

  const [messages, setMessages] = useState<Message[]>([
    {
      id: 'msg1',
      senderId: 'host1',
      senderName: 'Jessica Martinez',
      senderAvatar: '/api/placeholder/40/40',
      content: 'Hi! I saw you\'re interested in trying the Fenty Pro Filt\'r Foundation. I have shade 240 available.',
      timestamp: new Date(Date.now() - 1800000), // 30 minutes ago
      type: 'text',
      isRead: true
    },
    {
      id: 'msg2',
      senderId: 'user2',
      senderName: 'You',
      senderAvatar: '/api/placeholder/40/40',
      content: 'Yes! I\'ve been wanting to try that shade. Is it available this weekend?',
      timestamp: new Date(Date.now() - 1500000), // 25 minutes ago
      type: 'text',
      isRead: true
    },
    {
      id: 'msg3',
      senderId: 'host1',
      senderName: 'Jessica Martinez',
      senderAvatar: '/api/placeholder/40/40',
      content: 'Absolutely! Here\'s what it looks like:',
      timestamp: new Date(Date.now() - 1200000), // 20 minutes ago
      type: 'image',
      isRead: true,
      attachments: [{
        type: 'image',
        url: 'https://placehold.co/300x300?text=Fenty+Foundation',
        name: 'Fenty Foundation Shade 240'
      }]
    },
    {
      id: 'msg4',
      senderId: 'host1',
      senderName: 'Jessica Martinez',
      senderAvatar: '/api/placeholder/40/40',
      content: 'The Fenty foundation is perfect for your skin tone! When would you like to try it?',
      timestamp: new Date(Date.now() - 300000), // 5 minutes ago
      type: 'text',
      isRead: false,
      replyTo: 'msg2'
    }
  ]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = () => {
    if (!newMessage.trim()) return;

    const message: Message = {
      id: `msg${Date.now()}`,
      senderId: 'user2',
      senderName: 'You',
      senderAvatar: '/api/placeholder/40/40',
      content: newMessage,
      timestamp: new Date(),
      type: 'text',
      isRead: false,
      replyTo: replyingTo?.id
    };

    setMessages(prev => [...prev, message]);
    setNewMessage("");
    setReplyingTo(null);
  };

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const message: Message = {
        id: `msg${Date.now()}`,
        senderId: 'user2',
        senderName: 'You',
        senderAvatar: '/api/placeholder/40/40',
        content: 'Shared an image',
        timestamp: new Date(),
        type: 'image',
        isRead: false,
        attachments: [{
          type: 'image',
          url: URL.createObjectURL(file),
          name: file.name
        }]
      };

      setMessages(prev => [...prev, message]);
      setShowImageUpload(false);
    }
  };

  const handleReply = (message: Message) => {
    setReplyingTo(message);
  };

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  const getReplyMessage = (replyId: string) => {
    return messages.find(msg => msg.id === replyId);
  };

  const selectedConv = conversations.find(conv => conv.id === selectedConversation);

  return (
    <div className="h-screen bg-gradient-to-br from-secondary-light via-accent-cream to-primary-light flex">
      {/* Conversations List */}
      <div className="w-1/3 border-r border-accent-gold/20 bg-white/95 backdrop-blur-sm">
        <div className="p-4 border-b border-accent-gold/20">
          <h2 className="text-2xl font-display font-bold text-text-primary">Messages</h2>
          <p className="text-text-secondary">Stay connected with your beauty community</p>
        </div>
        
        <div className="overflow-y-auto h-full pb-20">
          {conversations.map((conversation) => (
            <motion.div
              key={conversation.id}
              whileHover={{ backgroundColor: 'rgba(212, 175, 55, 0.05)' }}
              className={`p-4 border-b border-accent-gold/10 cursor-pointer transition-colors duration-200 ${
                selectedConversation === conversation.id ? 'bg-accent-gold/10' : ''
              }`}
              onClick={() => setSelectedConversation(conversation.id)}
            >
              <div className="flex items-center space-x-3">
                <div className="relative">
                  <Avatar className="w-12 h-12">
                    <AvatarImage src={conversation.participant.avatar} />
                    <AvatarFallback>{conversation.participant.name[0]}</AvatarFallback>
                  </Avatar>
                  {conversation.participant.isOnline && (
                    <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 border-2 border-white rounded-full"></div>
                  )}
                </div>
                
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between">
                    <h3 className="font-display font-semibold text-text-primary truncate">
                      {conversation.participant.name}
                    </h3>
                    <div className="flex items-center space-x-2">
                      {conversation.isHost && (
                        <Badge className="bg-accent-gold text-white text-xs">Host</Badge>
                      )}
                      {conversation.unreadCount > 0 && (
                        <Badge className="bg-accent-gold text-white text-xs">
                          {conversation.unreadCount}
                        </Badge>
                      )}
                    </div>
                  </div>
                  
                  <p className="text-sm text-text-secondary truncate mt-1">
                    {conversation.lastMessage.content}
                  </p>
                  
                  <div className="flex items-center justify-between mt-1">
                    <span className="text-xs text-text-secondary">
                      {formatTime(conversation.lastMessage.timestamp)}
                    </span>
                    {!conversation.participant.isOnline && conversation.participant.lastSeen && (
                      <span className="text-xs text-text-secondary">
                        Last seen {formatTime(conversation.participant.lastSeen)}
                      </span>
                    )}
                  </div>
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>

      {/* Chat Area */}
      <div className="flex-1 flex flex-col">
        {selectedConv ? (
          <>
            {/* Chat Header */}
            <div className="p-4 border-b border-accent-gold/20 bg-white/95 backdrop-blur-sm">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-3">
                  <div className="relative">
                    <Avatar className="w-10 h-10">
                      <AvatarImage src={selectedConv.participant.avatar} />
                      <AvatarFallback>{selectedConv.participant.name[0]}</AvatarFallback>
                    </Avatar>
                    {selectedConv.participant.isOnline && (
                      <div className="absolute -bottom-1 -right-1 w-3 h-3 bg-green-500 border-2 border-white rounded-full"></div>
                    )}
                  </div>
                  <div>
                    <h3 className="font-display font-semibold text-text-primary">
                      {selectedConv.participant.name}
                    </h3>
                    <p className="text-sm text-text-secondary">
                      {selectedConv.participant.isOnline ? 'Online' : 'Offline'}
                    </p>
                  </div>
                </div>
                
                <div className="flex items-center space-x-2">
                  <Button variant="outline" size="sm" className="border-accent-gold text-accent-gold hover:bg-accent-gold hover:text-white">
                    ðŸ“ž Call
                  </Button>
                  <Button variant="outline" size="sm" className="border-accent-gold text-accent-gold hover:bg-accent-gold hover:text-white">
                    ðŸ“¹ Video
                  </Button>
                </div>
              </div>
            </div>

            {/* Messages */}
            <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-white/50">
              <AnimatePresence>
                {messages.map((message) => {
                  const isOwn = message.senderId === 'user2';
                  const replyMessage = message.replyTo ? getReplyMessage(message.replyTo) : null;
                  
                  return (
                    <motion.div
                      key={message.id}
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      exit={{ opacity: 0, y: -20 }}
                      className={`flex ${isOwn ? 'justify-end' : 'justify-start'}`}
                    >
                      <div className={`max-w-xs lg:max-w-md ${isOwn ? 'order-2' : 'order-1'}`}>
                        {/* Reply Context */}
                        {replyMessage && (
                          <div className="mb-2 p-2 bg-accent-cream/50 rounded-lg border-l-4 border-accent-gold">
                            <p className="text-xs text-text-secondary">
                              Replying to {replyMessage.senderName}
                            </p>
                            <p className="text-sm text-text-primary truncate">
                              {replyMessage.content}
                            </p>
                          </div>
                        )}
                        
                        <div
                          className={`relative group ${
                            isOwn
                              ? 'bg-accent-gold text-white'
                              : 'bg-white border border-accent-gold/20'
                          } rounded-2xl p-3 shadow-soft`}
                        >
                          {/* Message Content */}
                          {message.type === 'text' && (
                            <p className={isOwn ? 'text-white' : 'text-text-primary'}>
                              {message.content}
                            </p>
                          )}
                          
                          {message.type === 'image' && message.attachments && (
                            <div className="space-y-2">
                              <p className={isOwn ? 'text-white' : 'text-text-primary'}>
                                {message.content}
                              </p>
                              {message.attachments.map((attachment, index) => (
                                <img
                                  key={index}
                                  src={attachment.url}
                                  alt={attachment.name}
                                  className="rounded-lg max-w-full h-auto"
                                />
                              ))}
                            </div>
                          )}
                          
                          {/* Timestamp and Read Status */}
                          <div className={`flex items-center justify-between mt-2 text-xs ${
                            isOwn ? 'text-white/70' : 'text-text-secondary'
                          }`}>
                            <span>{formatTime(message.timestamp)}</span>
                            {isOwn && (
                              <span>{message.isRead ? 'âœ“âœ“' : 'âœ“'}</span>
                            )}
                          </div>
                          
                          {/* Reply Button */}
                          <button
                            onClick={() => handleReply(message)}
                            className="absolute -top-2 right-2 opacity-0 group-hover:opacity-100 transition-opacity duration-200 bg-accent-gold text-white text-xs px-2 py-1 rounded-full"
                          >
                            Reply
                          </button>
                        </div>
                      </div>
                      
                      {/* Avatar */}
                      <Avatar className={`w-8 h-8 ${isOwn ? 'order-1 mr-2' : 'order-2 ml-2'}`}>
                        <AvatarImage src={message.senderAvatar} />
                        <AvatarFallback>{message.senderName[0]}</AvatarFallback>
                      </Avatar>
                    </motion.div>
                  );
                })}
              </AnimatePresence>
              <div ref={messagesEndRef} />
            </div>

            {/* Reply Context */}
            {replyingTo && (
              <div className="p-3 bg-accent-cream/50 border-t border-accent-gold/20">
                <div className="flex items-center justify-between">
                  <div className="flex-1">
                    <p className="text-sm text-text-secondary">
                      Replying to {replyingTo.senderName}
                    </p>
                    <p className="text-sm text-text-primary truncate">
                      {replyingTo.content}
                    </p>
                  </div>
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => setReplyingTo(null)}
                    className="text-text-secondary hover:text-text-primary"
                  >
                    âœ•
                  </Button>
                </div>
              </div>
            )}

            {/* Message Input */}
            <div className="p-4 border-t border-accent-gold/20 bg-white/95 backdrop-blur-sm">
              <div className="flex items-center space-x-3">
                <div className="relative">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => setShowImageUpload(!showImageUpload)}
                    className="border-accent-gold text-accent-gold hover:bg-accent-gold hover:text-white"
                  >
                    ðŸ“Ž
                  </Button>
                  
                  {showImageUpload && (
                    <div className="absolute bottom-full mb-2 left-0 bg-white border border-accent-gold/20 rounded-lg shadow-medium p-2">
                      <input
                        ref={fileInputRef}
                        type="file"
                        accept="image/*"
                        onChange={handleImageUpload}
                        className="hidden"
                      />
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => fileInputRef.current?.click()}
                        className="w-full justify-start text-text-primary hover:bg-accent-gold/10"
                      >
                        ðŸ“· Photo
                      </Button>
                    </div>
                  )}
                </div>
                
                <Input
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder="Type your message..."
                  className="flex-1 border-accent-gold/30 focus:border-accent-gold"
                  onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
                />
                
                <Button
                  onClick={handleSendMessage}
                  disabled={!newMessage.trim()}
                  className="bg-accent-gold hover:bg-accent-darkGold text-white"
                >
                  Send
                </Button>
              </div>
            </div>
          </>
        ) : (
          <div className="flex-1 flex items-center justify-center bg-white/50">
            <div className="text-center">
              <div className="text-6xl mb-4">ðŸ’¬</div>
              <h3 className="text-2xl font-display font-bold text-text-primary mb-2">
                Select a conversation
              </h3>
              <p className="text-text-secondary">
                Choose a conversation from the list to start chatting
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default EnhancedChatInterface;
